.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1440px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 45px;
  padding-bottom: 54px;
  @media (max-width: 1440px) {
    max-width: 1140px;
  }
  @media (max-width: 1200px) {
    max-width: 960px;
    margin-bottom: 65px;
  }
  @media (max-width: 990px) {
    max-width: 100%;
  }
  @media (max-width: 320px) {
    margin-bottom: 0;
    padding-bottom: 32px;
  }
  &-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 320px) {
      align-items: flex-start;
    }
  }

  &__tel {
    color: $gray;
    transition: 0.3s ease;
    font-weight: 500;
    text-decoration: none;
    letter-spacing: 0.075rem;
    &:hover {
      color: $orange;
    }
    @media (max-width: 990px) {
      text-indent: -9999px;
      width: 2.5rem;
      height: 2.5rem;
      background-color: transparent;
      border: 1px solid $gray;
      background-image: url(../../../img/tel.svg);
      background-size: 1rem 0.87rem;
      background-position: center center;
      background-repeat: no-repeat;
      border-radius: 100%;
    }
    @media (max-width: 320px) {
      width: 2.1rem;
      height: 2.1rem;
    }
  }
  &__menu {
    @media (min-width: 991px) {
      display: none;
    }
    @media (max-width: 990px) {
      display: block;
      width: 2.5rem;
      height: 2.5rem;
      background-color: transparent;
      border: 1px solid $gray;
      background-image: url(../../../img/menu.svg);
      background-size: 1rem 0.87rem;
      background-position: center center;
      background-repeat: no-repeat;
      border-radius: 100%;
      margin-left: 20px;
      cursor: pointer;
      outline: none;
    }
    @media (max-width: 320px) {
      width: 2.2rem;
      height: 2.2rem;
      margin-left: 13px;
    }
  }
}
.header-card {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 79px;
  @media (max-width: 1440px) {
    margin-left: 40px;
  }
  @media (max-width: 1200px) {
    margin-left: 20px;
  }
  @media (max-width: 320px) {
    margin-left: 11px;
  }
  &__button {
    outline: none;
    width: 2.5rem;
    height: 2.5rem;
    background-color: $orange;
    border: none;
    border-radius: 100%;
    background-image: url("data:img/png;base64,");
    background-size: 16px;
    background-size: 1rem 0.87rem;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
    @media (max-width: 990px) {
      background-color: transparent;
      border: 1px solid $gray;
      background-image: url(../../../img/cart-black.svg);
    }
    @media (max-width: 320px) {
      width: 2.3rem;
      height: 2.3rem;
    }
    &:hover {
      background-color: $gray;
      @media (max-width: 990px) {
        background-color: $white;
      }
    }
  }
  &__count {
    color: $white;
    position: absolute;
    top: 6px;
    right: 9px;
    font-size: 0.5rem;
    @media (max-width: 990px) {
      color: $gray;
    }
    @media (max-width: 320px) {
      top: 3px;
      right: 11px;
    }
  }
}
button:focus {
  outline: none;
}
.logo {
  display: block;
  background-repeat: no-repeat;
  width: 120px;
  height: 32px;
  background-image: url(../../../img/logo.svg);
  margin-right: 120px;
  background-size: contain;
  @media (max-width: 1440px) {
    margin-right: 50px;
  }
  @media (max-width: 1200px) {
    margin-right: 32px;
  }
  @media (max-width: 320px) {
    width: 96px;
    margin-right: 61px;
  }
}
.nav {
  @media (max-width: 990px) {
    display: none;
  }
}
.nav {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &-item {
    margin-right: 48px;
    @media (max-width: 1440px) {
      margin-right: 30px;
    }
    @media (max-width: 1200px) {
      margin-right: 22px;
    }
  }
  &-link {
    color: $gray;
    text-decoration: none;
    transition: 0.3s ease;
    padding: 0;
    &:hover {
      color: $orange;
    }
  }
}
