.footer {
  border-top: 1px solid $gray;
  padding-top: 77px;
  padding-bottom: 60px;
  @media (max-width: 320px) {
    padding-top: 29px;
  }
  .logo {
    width: 179px;
    height: 48px;
    margin-top: -85px;
    @media (max-width: 320px) {
      width: 120px;
      height: 32px;
      margin: 10px auto;
      margin-bottom: 26px;
    }
  }
}
.box-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 320px) {
    display: block;
  }

  &-item {
    display: flex;
    justify-content: space-between;
    flex: 0 0 66.4%;
    @media (max-width: 1200px) {
      flex: 0 0 72%;
      align-items: center;
    }
    @media (max-width: 320px) {
      flex-direction: column;
      text-align: center;
      align-items: initial;
    }
  }
}
.box-footer-list {
  list-style-type: none;
  font-size: 1.12rem;
  @media (max-width: 320px) {
    margin-bottom: 23px;
  }
  &__menu {
    @media (max-width: 320px) {
      display: flex;
      justify-content: space-around;
      margin-bottom: 13px;
    }
  }
  &__item {
    margin-bottom: 1.62rem;
    @media (max-width: 320px) {
      font-size: 0.75rem;
      margin-bottom: 0.52rem;
    }
  }
  &__link {
    text-decoration: none;
    cursor: pointer;
    transition: 0.2s ease;
    color: $gray;
    @media (max-width: 320px) {
      font-size: 0.75rem;
    }
    &:hover {
      color: $orange;
    }
  }
  &__tel {
    font-size: 1.68rem;
    margin-bottom: 2.06rem;
    @media (max-width: 320px) {
      font-size: 1.68rem;
    }
  }
}

.box-footer-social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  @media (max-width: 320px) {
    justify-content: space-evenly;
    margin-top: 35px;
  }
  &__link {
    width: 48px;
    height: 48px;
    background-position: center center;
    background-repeat: no-repeat;
    opacity: 0.8;
    border-radius: 100%;
    display: block;
    border: 2px solid $gray;
    transition: 0.2s ease;
    &:hover {
      opacity: 1;
    }
  }

  &__vk {
    background-image: url(../../../img/vk.svg);
    background-size: 33px 23px;
  }
  &__inst {
    background-image: url(../../../img/instagram.svg);
    background-size: 18px 18px;
  }
  &__fb {
    background-image: url(../../../img/facebook.svg);
    background-size: 23px 18px;
  }
}
.box-copytight {
  display: flex;
  justify-content: space-between;
  margin-top: 42px;
  color: rgba($gray, 0.6);
  @media (max-width: 320px) {
    flex-direction: column;
    text-align: center;
    margin: 31px auto;
  }
  &__copy {
    @media (max-width: 320px) {
      font-size: 0.75rem;
    }
  }
  &__text {
    @media (max-width: 320px) {
      font-size: 0.75rem;
    }
  }
  &__link {
    text-decoration: none;
    font-size: 1.125rem;
    color: $gray;
    opacity: 0.6;
    transition: 0.2s ease;
    cursor: pointer;
    @media (max-width: 320px) {
      font-size: 0.625rem;
    }
    &:hover {
      color: $gray;
      opacity: 1;
    }
  }
}
