@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";
* {
  box-sizing: revert;
}
:root {
  font-size: 16px;
}
body {
  font-family: "Jost", sans-serif;
  color: $gray;
  overflow-x: hidden; //убирает нижжни  скролл
}
.title {
  font-family: "Cormorant", serif;
  font-size: 5rem;
  line-height: 1;
  @media (max-width: 320px) {
    font-size: 2rem;
  }
  span {
    font-style: italic;
  }
}
a {
  &:hover {
    text-decoration: none;
  }
}
.btn {
  outline: none;
  color: $white;
  background-color: $orange;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.3rem 2.68rem;
  border-radius: 2rem;
  letter-spacing: 0.075rem;
  box-shadow: 0px 10px 11.18px 1.82px rgba(207, 92, 13, 0.2);
  transition: 0.2s ease;
  @media (max-width: 320px) {
    padding: 0.8rem 1.5rem;
    border-radius: 1.3rem;
    font-size: 0.75rem;
    letter-spacing: 0.05rem;
  }

  &:hover {
    color: $white;
    background-color: $orangeHover;
  }
}
.ml {
  margin-left: 7.6rem;
  @media (max-width: 1200px) {
    margin-left: 0;
  }
}
.mr {
  margin-right: 7.6rem;
}

.wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  @media (max-width: 1440px) {
    max-width: 1140px;
  }
  @media (max-width: 1200px) {
    max-width: 960px;
  }
  @media (max-width: 990px) {
    max-width: 100%;
  }
}
.section-main {
  background-image: url(../img/bg-1.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-bottom: 208px;
  position: relative;
  @media (max-width: 320px) {
    padding-bottom: 56px;
  }
  &__image {
    position: absolute;
  }
  &__tomato {
    right: 111px;
    bottom: 140px;
    @media (max-width: 1700px) {
      right: 10px;
    }
    @media (max-width: 991px) {
      left: -10px;
      top: 211px;
      width: 75px;
    }
    @media (max-width: 991px) {
      left: -10px;
      top: 97px;
      width: 49px;
    }
  }
  &__beans {
    left: -8px;
    bottom: 179px;
    @media (max-width: 1700px) {
      display: none;
    }
  }
  &__pepper {
    left: 28px;
    top: 403px;
    @media (max-width: 1700px) {
      left: -1px;
      top: 50%;
    }
    @media (max-width: 990px) {
      left: -8px;
      top: 37%;
      width: 75px;
    }
    @media (max-width: 320px) {
      left: -5px;
      top: 40.5%;
      width: 33px;
    }
  }
  &__cucumber {
    left: 50%;
    top: 1px;
    @media (max-width: 990px) {
      transform: rotate(90deg);
      left: 93%;
      top: 65%;
    }
    @media (max-width: 320px) {
      left: 90%;
      top: 74%;
      width: 45px;
    }
  }
  &__avocado {
    right: -26px;
    top: 50px;
    @media (max-width: 1700px) {
      right: -61px;
      top: 101px;
      width: 129px;
    }
    @media (max-width: 320px) {
      right: -49px;
      top: 97px;
      width: 90px;
    }
  }
}
.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 1200px) {
    flex-direction: column;
  }
  &-text {
    flex: 0 0 40%;
    @media (max-width: 1440px) {
      flex: 0 0 45%;
    }
    @media (max-width: 1200px) {
      text-align: center;
      margin-bottom: 100px;
    }
    @media (max-width: 320px) {
      margin-bottom: 0;
    }
    &__description {
      font-size: 1.12rem;
      line-height: 1.51;
      margin-top: 60px;
      margin-bottom: 77px;
      @media (max-width: 1200px) {
        max-width: 559px;
        margin-bottom: 45px;
      }
      @media (max-width: 320px) {
        max-width: 245px;
        margin: 19px auto;
        text-align: center;
        font-size: 0.75rem;
      }
    }
    &__image {
      @media (min-width: 1201px) {
        display: none;
      }
      @media (max-width: 1200px) {
        display: block;
        width: 427px;
        margin: 0 auto;
        margin-bottom: 65px;
      }
      @media (max-width: 320px) {
        width: 217px;
        margin-bottom: 27px;
        margin-top: 31px;
      }
    }
  }
  &-visual {
    border: 1px solid rgba($gray, $alpha);
    border-radius: 100%;
    padding: 53px;
    position: relative;
    @media (max-width: 1200px) {
      display: none;
    }
    &__image {
      border: 1px solid rgba($gray, $alpha);
      border-radius: 100%;
      padding: 21px;
      img {
        border-radius: 100%;
        box-shadow: 11px 48px 42px 0px rgba($gray, $alpha);
        @media (max-width: 1440px) {
          width: 427px;
        }
      }
    }
    &__btn {
      background-color: transparent;
      border: none;
      color: rgba($gray, 0.8);
      text-align: left;
      position: absolute;
      span {
        color: rgba($gray, 1);
        display: block;
        margin-top: 6px;
      }
      &::before {
        content: "";
        display: block;
        width: 11px;
        height: 11px;
        border-radius: 100%;
        background-color: $gray;
        box-shadow: 0 0 0 4px #fff, 0 0 0 5px $gray;
        position: absolute;
      }
      &:focus {
        outline: none;
        &::before {
          background-color: $orange;
          box-shadow: 0 0 0 4px #fff, 0 0 0 5px $orange;
        }
      }
    }
    &__01 {
      left: 62px;
      top: 43px;
      @media (max-width: 1440px) {
        left: 70px;
        top: 58px;
      }
      &::before {
        left: 91px;
        top: 23px;
      }
    }
    &__02 {
      right: 0px;
      top: 190px;
      @media (max-width: 1440px) {
        right: -34px;
        top: 190px;
      }
      &::before {
        right: 94px;
        top: 18px;
      }
    }
    &__03 {
      right: 232px;
      bottom: -47px;
      @media (max-width: 1440px) {
        right: 232px;
        bottom: -62px;
      }
      &::before {
        right: 37px;
        bottom: 56px;
      }
    }
  }
}

.section-stock {
  background-image: url(../img/bg-2.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-bottom: 160px;
  @media (max-width: 320px) {
    padding-bottom: 49px;
  }
  .title {
    @media (max-width: 1200px) {
      text-align: center;
    }
    @media (max-width: 320px) {
      font-size: 1.87rem;
    }
  }
  &__text {
    font-size: 1.5rem;
    line-height: 1.5;
    max-width: 690px;
    margin-top: 3.35rem;
    margin-bottom: 5.65rem;
    @media (max-width: 1200px) {
      text-align: center;
      margin: 30px auto;
    }
    @media (max-width: 320px) {
