    }
  }
}
.slider {
  &-child {
    padding: 5px 10px;
    box-sizing: border-box;
    &:focus {
      outline: none;
    }
  }
  &-item {
    border: 1px solid rgba($gray, $alpha);
    border-radius: 20px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 520px;
    position: relative;
    padding: 2rem 2.25rem;
    box-sizing: border-box;
    box-shadow: 0 0 0 2px rgba($gray, 0);
    transition: 0.3s ease;
    overflow: hidden;
    @media (max-width: 1200px) {
      text-align: center;
    }
    @media (max-width: 320px) {
      min-height: 240px;
      width: 208px;
      background-position: 11px 1px;
      background-size: 138px;
    }
    &:hover {
      box-shadow: 0 0 0 2px rgba($gray, $alpha);
    }
    &:hover {
      .slider-text {
        transform: translate(0, 0);
      }
    }
  }
  &-hover {
    transition: 0.3s ease;
    overflow: hidden;
    padding: 2.75rem 2.25rem;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 20px;
      transition: 0.3s ease;
    }
    &:hover::before {
      background-color: rgba($white, 0.55);
    }
  }
  &-milk {
    background-image: url(../img/stock/milk-bg.png);
  }
  &-pasta {
    background-image: url(../img/stock/pasta.png);
  }
  &-chicken {
    background-image: url(../img/stock/steaks.png);
    @media (max-width: 320px) {
      background-position: -7px 1px;
      background-size: 213px;
    }
  }
  &-text {
    position: absolute;
    bottom: 2.4rem;
    left: 2.25rem;
    max-width: 58%;
    transform: translate(0, 242px);
    transition: 0.3s ease;
    @media (max-width: 1440px) {
      transform: translate(0, 255px);
      max-width: 80%;
    }
    @media (max-width: 1200px) {
      transform: translate(0, 205px);
    }
    @media (max-width: 990px) {
      transform: translate(0, 248px);
    }
    @media (max-width: 790px) {
      transform: translate(0, 250px);
    }
    @media (max-width: 590px) {
      transform: translate(0, 320px);
    }
    @media (max-width: 320px) {
      position: relative;
      transform: none;
      max-width: none;
      bottom: -114px;
      left: 1px;
      text-align: center;
    }
    &__title {
      font-size: 1.875rem;
      font-family: "Cormorant", serif;
      margin-bottom: 26px;
      @media (max-width: 320px) {
        font-size: 0.87rem;
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }
    &__description {
      color: rgba($gray, 0.7);
      margin-bottom: 41px;
      line-height: 1.5;
      @media (max-width: 320px) {
        display: none;
      }
    }
    &__link {
      color: $gray;
      text-transform: uppercase;
      text-decoration: none;
      font-weight: 500;
      letter-spacing: 0.075rem;
      opacity: 0.8;
      position: relative;
      padding: 51px;
      transition: 0.3s ease;
      @media (max-width: 320px) {
        font-size: 0.625rem;
        padding: 0;
      }
      &:hover {
        color: $gray;
        opacity: 1;
        padding: 45px;
      }
      &:focus {
        outline: none;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: calc(50% - 0.93rem); //без пробела у минуса не работает)))
        display: block;
        width: 1.875rem;
        height: 1.875rem;
        background-image: url(../img/plus.svg);
        background-repeat: no-repeat;
        @media (max-width: 320px) {
          left: -25px;
          top: calc(50% - 0.8rem);
          width: 1.3rem;
          height: 1.3rem;
        }
      }
    }
  }
  &-thumb {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 314px;
    margin-bottom: 40px;
    @media (max-width: 320px) {
      height: 126px;
      margin-bottom: 0;
      margin-top: -21px;
    }
    &__image {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
      @media (max-width: 320px) {
        width: 126px;
      }
    }
  }
}
.slider-info {
  &__title {
    font-size: 1.875rem;
    font-family: "Cormorant", serif;
    margin-bottom: 26px;
    @media (max-width: 1440px) {
