    }
    @media (max-width: 320px) {
      font-size: 0.875rem;
      min-height: 0;
      margin-bottom: 11px;
    }
  }
  &__description {
    font-size: 1.12rem;
    color: rgba($gray, 0.7);
    line-height: 1.5;
    margin-bottom: 0;
    min-height: 52px;
    max-width: 96%;
    @media (max-width: 320px) {
      font-size: 0.75rem;
    }
  }
  &__price {
    font-size: 1.5rem;
    margin-top: 25px;
    @media (max-width: 320px) {
      font-size: 0.875rem;
