}
.slider-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 28px;
  margin-bottom: 8px;
  &__btn {
    border: none;
    padding: 1.4rem 3.2rem;
    cursor: pointer;
    margin-left: 20px;
    @media (max-width: 1440px) {
      padding: 1rem 1.8rem;
      margin-left: 2px;
    }
    @media (max-width: 1200px) {
      margin-top: 15px;
      margin-left: 0;
      display: block;
      width: 100%;
    }
    @media (max-width: 320px) {
      padding: 0.7rem 1.3rem;
      font-size: 0.65rem;
    }
  }
}
.slider-count {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  @media (max-width: 1200px) {
    display: none;
  }
  &__minus,
  &__plus {
    display: block;
    width: 3.12rem;
    height: 3.12rem;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    opacity: 0.4;
    transition: 0.2s ease;
    &:hover {
      opacity: 1;
    }
    @media (max-width: 1440px) {
      width: 2.5rem;
      height: 2.5rem;
    }
  }
  &__number {
    border: none;
    width: 50px;
    text-align: center;
    outline: none;
    @media (max-width: 1440px) {
      width: 20px;
    }
  }
  &__minus {
    background-image: url(../img/minus.svg);
  }
  &__plus {
    background-image: url(../img/plus.svg);
  }
}
.slick-prev,
.slick-next {
  position: absolute;
  background-color: transparent;
  border: none;
  top: -64px;
  text-indent: -9999px;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  width: 10px;
  height: 18px;
  opacity: 0.4;
  transition: 0.3s ease;
  cursor: pointer;
  &:hover {
    opacity: 1;
  }
  &:focus {
    outline: none;
  }
}
.slick-prev {
  right: 82px;
  background-image: url(../img/arrow-prev.svg);
}
.slick-next {
  right: 0;
  background-image: url(../img/arrow-next.svg);
}
.slick-dots {
  display: flex;
  list-style-type: none;
  justify-content: center;
  text-indent: -9999px;
  margin-top: 30px;
  & > li {
    width: 10px;
    height: 10px;
    background: $gray;
    margin-right: 10px;
    margin-left: 10px;
    border-radius: 100%;
  }
  & > li.slick-active {
    background: $orange;
  }
}
