  }
  &-item {
    border: 1px solid rgba($gray, $alpha);
    border-radius: 20px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 515px;
    position: relative;
    padding: 2rem 1.9rem;
    box-sizing: border-box;
    box-shadow: 0 0 0 2px rgba($gray, 0);
    transition: 0.3s ease;
    overflow: hidden;
    flex: 0 0 24%;
    margin-bottom: 20px;
    @media (max-width: 1200px) {
      flex: 0 0 49%;
      text-align: center;
    }
    @media (max-width: 320px) {
      min-height: 237px;
      padding: 37px 10px;
    }
    &:hover {
      box-shadow: 0 0 0 2px rgba($gray, $alpha);
    }
    &:hover {
      .slider-text {
        transform: translate(0, 0);
      }
    }
  }
  &-thumb {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 204px;
    margin-bottom: 14px;
    @media (max-width: 320px) {
      max-height: 87px;
      margin-top: -46px;
      margin-bottom: 0;
    }
    &__image {
      max-height: 204px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
      @media (max-width: 320px) {
        max-height: 60px;
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }
  }
}

.catalog-info {
  &__title {
    font-size: 1.875rem;
    font-family: "Cormorant", serif;
    margin-bottom: 17px;
    @media (max-width: 1440px) {
      min-height: 80px;
    }
    @media (max-width: 320px) {
      font-size: 0.875rem;
      margin-bottom: 5px;
      min-height: 0;
    }
  }
  &__description {
    color: rgba($gray, 0.7);
    line-height: 1.5;
    margin-bottom: 0;
    min-height: 52px;
    max-width: 96%;
    @media (max-width: 1440px) {
      min-height: 80px;
    }
    @media (max-width: 320px) {
      font-size: 0.625rem;
      min-height: 0;
    }
  }
  &__price {
    font-size: 1.5rem;
    margin-top: 8px;
    @media (max-width: 320px) {
      font-size: 0.875rem;
    }
  }
}
.catalog-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 28px;
  margin-bottom: 8px;
  @media (max-width: 1440px) {
    flex-direction: column;
  }
  &__btn {
    font-size: 0.875rem;
    border: none;
    padding: 1.3rem 2rem;
    cursor: pointer;
    margin-left: 17px;
    @media (max-width: 1440px) {
      margin-top: 15px;
      margin-left: 0;
      display: block;
      width: 100%;
    }
    @media (max-width: 320px) {
      width: 100px;
      padding: 0.8rem 0.5rem;
    }
  }
  .catalog-count {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    @media (max-width: 1200px) {
      display: none;
    }
    @media (max-width: 1440px) {
      display: flex;
      width: 100%;
    }
    &__minus,
    &__plus {
      display: block;
      width: 2.5rem;
      height: 2.5rem;
      background-repeat: no-repeat;
      background-size: cover;
      cursor: pointer;
      opacity: 0.4;
      transition: 0.2s ease;
      &:hover {
        opacity: 1;
      }
    }
    &__number {
      border: none;
      width: 30px;
      text-align: center;
      outline: none;
    }
    &__minus {
      background-image: url(../img/minus.svg);
    }
    &__plus {
      background-image: url(../img/plus.svg);
    }
  }
}
.btn-load {
  position: relative;
  border: none;
  background-color: transparent;
  text-transform: uppercase;
  letter-spacing: 0.06rem;
  opacity: 0.8;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding-left: 2.43rem;
  transition: 0.2s ease;
  @media (max-width: 320px) {
    font-size: 65%;
    padding-left: 1.73rem;
  }
  cursor: pointer;
  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 0.78rem);
    left: 0;
    display: block;
    background-image: url(../img/refresh.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 1.56rem;
    height: 1.56rem;
    @media (max-width: 320px) {
      width: 1.12rem;
      height: 1.12rem;
      top: calc(50% - 0.68rem);
    }
  }
  &:hover {
    opacity: 1;
  }
  &:focus {
    outline: none;
  }
}
.section-delivery {
  background-image: url(../img/bg-6.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-bottom: 173px;
  @media (max-width: 1200px) {
    text-align: center;
  }
  @media (max-width: 320px) {
    padding-bottom: 55px;
  }
  .title {
    @media (max-width: 320px) {
      font-size: 1.87rem;
    }
  }
}
.box-delivery {
  display: flex;
  justify-content: space-between;
  margin-top: 55px;
  @media (max-width: 320px) {
    display: block;
    margin-top: 34px;
  }
  &-item {
    .mr {
      @media (max-width: 320px) {
        margin-right: 0;
      }
    }
    flex: 0 0 49%;
    &:last-child {
      flex: 0 0 41%;
    }
  }
  &__text {
    font-size: 1.12rem;
    line-height: 1.5;
    margin-bottom: 28px;
    @media (max-width: 320px) {
      font-size: 0.75rem;
      max-width: 281px;
    }
  }
  &__link {
    color: $gray;
    border-bottom: 1px solid $gray;
    text-decoration: none;
    &:hover {
      color: $gray;
    }
  }
  &__span {
    color: $orange;
  }
}
.box-delivery-list {
  display: flex;
  list-style-type: none;
  margin-top: 46px;
  @media (max-width: 320px) {
    margin: 0 auto;
    margin-left: 45px;
  }
  &__item {
    display: block;
    background-repeat: no-repeat;
    height: 32px;
    margin-right: 1.93rem;
    @media (max-width: 320px) {
      margin-right: 0.63rem;
    }
  }
  &__mc {
    background-image: url(../img/mastercard.svg);
    width: 53px;
    background-size: 53px 32px;
    opacity: 0.4;
    cursor: pointer;
    @media (max-width: 320px) {
      background-size: 40px 32px;
    }
    &:hover {
      opacity: 1;
    }
  }
  &__visa {
    background-image: url(../img/visa.svg);
    width: 74px;
    background-size: 74px 32px;
    opacity: 0.4;
    background-position: 0 - 2px;
    cursor: pointer;
    @media (max-width: 320px) {
      background-size: 53px 16px;
      width: 63px;
    }
    &:hover {
      opacity: 1;
    }
  }
  &__paypal {
    align-items: center;
    background-image: url(../img/paypal.svg);
    width: 87px;
    background-size: 87px 24px;
    background-position: 0 4px;
    opacity: 0.4;
    cursor: pointer;
    @media (max-width: 320px) {
      background-size: 61px 22px;
      background-position: 0 6px;
    }
    &:hover {
      opacity: 1;
    }
  }
}
.section-form {
  background-image: url(../img/bg-7.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.box-form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 160px;
  @media (max-width: 1200px) {
    flex-direction: column;
  }
  @media (max-width: 320px) {
    padding-bottom: 60px;
  }
  .title {
    font-family: "Cormorant", serif;
    font-size: 5rem;
    line-height: 1;
    margin-bottom: 60px;
    @media (max-width: 320px) {
      font-size: 1.87rem;
    }
  }
  &-item {
    flex: 0 0 39%;
    @media (max-width: 1200px) {
      text-align: center;
    }
    &:last-child {
      flex: 0 0 41%;
    }
  }
  &__text {
    max-width: 460px;
    font-size: 1.1rem;
    line-height: 1.5;
    @media (max-width: 1200px) {
      max-width: 550px;
    }
    @media (max-width: 320px) {
      font-size: 0.75rem;
      max-width: 255px;
      margin: auto;
    }
  }
}
.box-form-list {
  list-style-type: none;
  margin-top: 62px;
  margin-bottom: 20px;
  @media (max-width: 1200px) {
    display: flex;
    justify-content: center;
  }
  &__item {
    position: relative;
    padding-left: 58px;
    @media (max-width: 320px) {
      padding-left: 28px;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &::before {
      content: "";
      display: block;
      width: 29px;
      height: 25px;
      background-repeat: no-repeat;
      position: absolute;
      left: 0;
      top: calc(50% - 12.5px);
      @media (max-width: 320px) {
        background-size: 18px;
      }
    }
  }
  &__tel {
    @media (max-width: 1200px) {
      margin-right: 20px;
    }
    &::before {
      background-image: url("data:img/png;base64,");
      @media (max-width: 320px) {
        top: calc(50% - 8.5px);
      }
    }
  }
  &__email {
    &::before {
      background-image: url("data:img/png;base64,");
      background-position: 0 center;
    }
  }
  &__link {
    color: $gray;
    text-decoration: none;
    font-size: 1.5rem;
    font-style: 500;
    @media (max-width: 320px) {
      font-size: 0.75rem;
    }
    &:hover {
      color: $orange;
    }
  }
}
.form {
  border: 2px solid rgba($gray, $alpha);
  border-radius: 20px;
  padding: 2rem 3.5rem 1.8rem 3.5rem;
  @media (max-width: 320px) {
    padding: 0.8rem 1.2rem;
  }
  &__h3 {
    font-family: "Cormorant", serif;
    font-size: 1.87rem;
    margin-bottom: 30px;
    font-style: 500;
    @media (max-width: 320px) {
      font-size: 1.37rem;
      margin-bottom: 20px;
    }
  }
  &__label {
    display: block;
    font-size: 0.75rem;
    margin-bottom: 10px;
    margin-left: 5px;
    @media (max-width: 320px) {
      font-size: 0.65rem;
      text-align: left;
      margin-bottom: 2px;
    }
  }
  &__input {
    display: block;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba($gray, $alpha);
    border-radius: 20px;
    padding: 1.4rem 1.9rem;
    margin-bottom: 29px;
    transition: 0.2s ease;
    @media (max-width: 320px) {
      padding: 1rem 0.9rem;
      font-size: 75%;
      margin-bottom: 10px;
    }
    &:focus {
      outline: none;
      border: 1px solid ($gray);
    }
  }
  &__text {
    font-size: 0.75rem;
    opacity: 0.6;
    margin-top: 15px;
    line-height: 1.5;
    @media (max-width: 320px) {
      font-size: 0.65rem;
      max-width: 240px;
    }
  }
  &__link {
    color: $gray;
    opacity: 0.6;
    border-bottom: 1px solid rgba($gray, 0.6);
    text-decoration: none;
    &:hover {
      color: $gray;
      opacity: 1;
    }
  }
  .btn {
    margin-top: 11px;
    border: none;
    padding: 1.5rem 3.1rem;
    cursor: pointer;
    @media (max-width: 320px) {
      padding: 0.9rem 1.9rem;
      margin-top: 20px;
    }
  }
}
